<template>
    <div id="Service">
        <!-- 页面顶部横幅 -->
        <div class="service-banner">
            <div class="banner-overlay"></div>
            <div class="banner-content text-center">
                <h1 class="wow fadeInUp">我们的服务</h1>
                <div class="banner-divider wow fadeInUp" data-wow-delay="0.2s"></div>
                <p class="wow fadeInUp" data-wow-delay="0.4s">传统智慧与现代科技的完美融合</p>
            </div>
        </div>
        
        <div class="container service-section">
            <div class="section-header text-center wow fadeInUp">
                <span class="section-subtitle">专业服务</span>
                <h2 class="section-title">为您提供全方位的命运科学解决方案</h2>
                <div class="section-divider center-divider"></div>
                <p class="section-description">我们汇聚了精通玄学文化、生命科学、环境科学及天文学等领域的顶尖专家与学者</p>
            </div>
            
            <div class="service-cards-container">
                <div class="service-card wow fadeInUp" 
                    v-for="(item,index) in serviceList" 
                    :key="index" 
                    @click="ServiceClick(item.id)"
                    :data-wow-delay="0.2 * index + 's'">
                    <div class="service-card-inner">
                        <div class="service-card-front">
                            <div class="service-card-img">
                                <img :src="item.img" alt="服务">
                                <div class="service-card-overlay"></div>
                            </div>
                            <div class="service-card-content">
                                <h3>{{item.title}}</h3>
                                <div class="service-card-divider"></div>
                                <p>{{item.eng_title}}</p>
                            </div>
                        </div>
                        <div class="service-card-back">
                            <div class="service-card-back-content">
                                <h3>{{item.title}}</h3>
                                <div class="service-card-divider"></div>
                                <p>点击了解更多详情</p>
                                <div class="service-card-btn">
                                    <span>查看详情</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
        <!-- 服务优势部分 -->
        <div class="service-features">
            <div class="container">
                <div class="features-header text-center wow fadeInUp">
                    <h2>为什么选择我们的服务</h2>
                    <div class="features-divider"></div>
                </div>
                <div class="row features-row justify-content-center text-center">
                    <div class="col-md-8 col-sm-12 wow fadeInUp">
                        <div class="features-content">
                            <p class="features-intro">知命者文化以严谨的科学态度与深厚的文化底蕴，为每一位用户制定个性化方案，能够协助每个人优化生命轨迹，重塑人生价值。</p>
                            <div class="features-items">
                                <div class="feature-item">
                                    <div class="feature-icon mx-auto">
                                        <i class="fa fa-users"></i>
                                    </div>
                                    <div class="feature-text">
                                        <h4>专业团队支持</h4>
                                        <p>顶尖专家提供全方位指导</p>
                                    </div>
                                </div>
                                <div class="feature-item">
                                    <div class="feature-icon mx-auto">
                                        <i class="fa fa-balance-scale"></i>
                                    </div>
                                    <div class="feature-text">
                                        <h4>传统与现代融合</h4>
                                        <p>结合古老智慧与前沿科技</p>
                                    </div>
                                </div>
                                <div class="feature-item">
                                    <div class="feature-icon mx-auto">
                                        <i class="fa fa-cogs"></i>
                                    </div>
                                    <div class="feature-text">
                                        <h4>个性化定制方案</h4>
                                        <p>针对您的独特需求</p>
                                    </div>
                                </div>
                                <div class="feature-item">
                                    <div class="feature-icon mx-auto">
                                        <i class="fa fa-check-circle"></i>
                                    </div>
                                    <div class="feature-text">
                                        <h4>科学验证体系</h4>
                                        <p>确保服务效果可靠</p>
                                    </div>
                                </div>
                            </div>
                            <button class="btn-contact" @click="contactUs">联系我们</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { WOW } from 'wowjs';
export default {
    name: 'Service',
    data(){
        return{
            serviceList: [
                {
                    id: 'section-1',
                    title: '命运科学探究与咨询',
                    eng_title: 'Fate Research & Consultation',
                    img: require('@/assets/img/service1.jpeg')
                },{
                    id: 'section-2',
                    title: '生命状态综合诊断',
                    eng_title: 'Comprehensive Life Diagnosis',
                    img: require('@/assets/img/service2.jpeg')
                },{
                    id: 'section-3',
                    title: '智能环境与运程优化',
                    eng_title: 'Smart Destiny Optimization',
                    img: require('@/assets/img/service3.jpeg')
                },{
                    id: 'section-4',
                    title: '导师团队定制化指导',
                    eng_title: 'Expert Mentorship Programs',
                    img: require('@/assets/img/service4.jpeg')
                }
            ]
        }
    },
    mounted(){
        var wow = new WOW({
            boxClass: 'wow',
            animateClass: 'animated',
            offset: 100,
            mobile: true,
            live: true
        });
        wow.init();
    },
    methods:{
        ServiceClick(id){
            this.$router.push({
                name: 'servicedetail',
                params: {
                    id: id
                }
            })
        },
        contactUs() {
            this.$router.push({
                name: 'contactus'
            })
        }
    }
}
</script>

<style scoped>
/* 全局样式 */
#Service {
    font-family: 'Helvetica Neue', Arial, sans-serif;
    color: #333;
    line-height: 1.6;
    padding-top: 90px; /* 防止内容被header遮挡 */
    overflow-x: hidden;
}

/* 横幅样式 */
.service-banner {
    position: relative;
    height: 350px;
    background-image: url("../assets/img/service-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #fff;
    margin-bottom: 50px;
    width: 100vw;
    left: 50%;
    right: 50%;
    margin-left: -50vw;
    margin-right: -50vw;
    max-width: 100vw;
}

.banner-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
}

.banner-content {
    position: relative;
    z-index: 1;
    max-width: 800px;
    padding: 0 20px;
}

.banner-content h1 {
    font-size: 48px;
    font-weight: 700;
    margin-bottom: 20px;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.banner-content p {
    font-size: 20px;
    margin-bottom: 30px;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.banner-divider {
    width: 80px;
    height: 3px;
    background-color: #fff;
    margin: 0 auto 20px;
}

/* 章节标题样式 */
.service-section {
    padding: 50px 0;
}

.section-header {
    margin-bottom: 60px;
}

.section-subtitle {
    display: inline-block;
    font-size: 16px;
    color: #3498db;
    font-weight: 500;
    margin-bottom: 10px;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.section-title {
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 20px;
    color: #2c3e50;
}

.section-divider {
    width: 80px;
    height: 3px;
    background: linear-gradient(to right, #3498db, #2c3e50);
    margin-bottom: 20px;
}

.center-divider {
    margin: 0 auto 20px;
}

.section-description {
    font-size: 18px;
    color: #666;
    max-width: 800px;
    margin: 0 auto 40px;
}

/* 服务卡片容器 */
.service-cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

/* 服务卡片样式 - 新设计 */
.service-card {
    width: calc(50% - 30px);
    min-width: 280px;
    max-width: 500px;
    height: 280px;
    perspective: 1000px;
    cursor: pointer;
    margin-bottom: 20px;
}

.service-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
}

.service-card:hover .service-card-inner {
    transform: rotateY(180deg);
}

.service-card-front, .service-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 12px;
    overflow: hidden;
}

.service-card-front {
    background-color: #fff;
}

.service-card-back {
    background: linear-gradient(135deg, #3498db, #2c3e50);
    color: white;
    transform: rotateY(180deg);
    display: flex;
    align-items: center;
    justify-content: center;
}

.service-card-img {
    height: 65%;
    position: relative;
    overflow: hidden;
}

.service-card-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.service-card:hover .service-card-img img {
    transform: scale(1.1);
}

.service-card-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(0,0,0,0.1), rgba(0,0,0,0.4));
}

.service-card-content {
    padding: 20px;
    height: 35%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.service-card-content h3 {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 8px;
    color: #2c3e50;
}

.service-card-content p {
    font-size: 14px;
    color: #666;
    margin: 0;
}

.service-card-divider {
    width: 40px;
    height: 2px;
    background-color: #3498db;
    margin: 8px auto;
}

.service-card-back-content {
    padding: 30px;
    text-align: center;
}

.service-card-back-content h3 {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 10px;
}

.service-card-back-content p {
    margin-bottom: 20px;
}

.service-card-btn {
    display: inline-block;
    padding: 10px 25px;
    background-color: rgba(255, 255, 255, 0.2);
    border: 2px solid #fff;
    border-radius: 30px;
    color: #fff;
    font-weight: 500;
    transition: all 0.3s ease;
}

.service-card-btn:hover {
    background-color: #fff;
    color: #3498db;
}

/* 服务特点部分 - 居中设计 */
.features-row {
    display: flex;
    align-items: center;
    justify-content: center;
}

.features-content {
    padding: 30px 0;
    text-align: center;
    max-width: 800px;
    margin: 0 auto;
}

.features-intro {
    font-size: 16px;
    color: #555;
    line-height: 1.8;
    margin-bottom: 30px;
    text-align: center;
}

.features-items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 25px;
    margin-bottom: 30px;
}

.feature-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.feature-icon {
    width: 50px;
    height: 50px;
    background-color: rgba(52, 152, 219, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;
}

.feature-icon i {
    font-size: 20px;
    color: #3498db;
}

.feature-text {
    text-align: center;
}

.feature-text h4 {
    font-size: 18px;
    font-weight: 600;
    color: #2c3e50;
    margin-bottom: 5px;
}

.feature-text p {
    font-size: 14px;
    color: #666;
    margin: 0;
}

.btn-contact {
    display: inline-block;
    margin: 0 auto;
    padding: 12px 30px;
    background-color: #3498db;
    color: #fff;
    border: none;
    border-radius: 30px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 5px 15px rgba(52, 152, 219, 0.3);
}

.btn-contact:hover {
    background-color: #2980b9;
    transform: translateY(-3px);
    box-shadow: 0 8px 20px rgba(52, 152, 219, 0.4);
}

.features-image {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.features-image img {
    max-width: 90%;
    border-radius: 12px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

/* 响应式调整 */
@media (max-width: 991px) {
    .service-banner {
        height: 300px;
    }
    
    .banner-content h1 {
        font-size: 36px;
    }
    
    .banner-content p {
        font-size: 18px;
    }
    
    .section-title {
        font-size: 32px;
    }
    
    .service-card {
        width: calc(100% - 30px);
        max-width: 450px;
    }
    
    .features-items {
        grid-template-columns: 1fr;
    }
    
    .features-content {
        margin-bottom: 30px;
    }
}

@media (max-width: 767px) {
    .service-banner {
        height: 250px;
    }
    
    .banner-content h1 {
        font-size: 30px;
    }
    
    .banner-content p {
        font-size: 16px;
    }
    
    .section-title {
        font-size: 28px;
    }
    
    .service-card {
        height: 260px;
    }
}

@media (max-width: 480px) {
    .service-banner {
        height: 200px;
    }
    
    .banner-content h1 {
        font-size: 24px;
    }
    
    .banner-content p {
        font-size: 14px;
    }
    
    .section-title {
        font-size: 24px;
    }
    
    .service-card {
        height: 240px;
    }
    
    .service-card-content h3 {
        font-size: 16px;
    }
    
    .service-card-content p {
        font-size: 12px;
    }
    
    .feature-icon {
        width: 40px;
        height: 40px;
    }
    
    .feature-text h4 {
        font-size: 16px;
    }
    
    .feature-text p {
        font-size: 12px;
    }
}
</style>




